import React, { useEffect, useState } from "react"
import styled from "styled-components"
import Indicator from "../atoms/Indicator"
import WebsiteContent from "./WebsiteContent"

const StyledContainer = styled.div`
  width: 70%;
  padding: 30px 0;
  margin: 0 auto;
  @media (min-width: 900px) {
    min-height: 1000px;
  }
  @media (max-width: 900px) {
    width: 95%;
    flex-direction: column;
    align-items: center;
  }
  display: flex;
  transition: 0.3 ease-in-out;
  position: relative;
  cursor: default;
  overflow: hidden;
`

const TitleImageContainer = styled.div`
  position: relative;
  width: 80%;
  display: flex;
  flex-direction: column;
  text-align: left;
  @media (max-width: 900px) {
    width: 100%;
    text-align: center !important;
  }
`

const StyledTitle = styled.h1`
  font-size: 4vw;
  font-weight: 300;
  margin: 20px 0;
  display: inline-block;
  text-transform: uppercase;
  color: black;
  position: relative;
  @media (max-width: 920px) {
    font-size: 9vw;
  }
`

const WebsiteImage = styled.img`
  position: relative;
  overflow: hidden;
  width: 100%;
  overflow: hidden;
  box-shadow: 0 25px 35px rgba(0, 0, 0, 0.3);
  @media (max-width: 900px) {
    z-index: 10;
    margin: 0 auto;
    box-shadow: none;
  }
`

const WebsiteCard = ({
  src,
  color,
  alt,
  link,
  title,
  description,
  className,
  technologies,
}) => {
  useEffect(() => {
    const HideAndShowContent = () => {
      const containers = document.getElementsByClassName("container")
      Array.from(containers).forEach(container => {
        document.fonts.ready
          .then(() => {
            const containerContent = container.getElementsByClassName(
              "WebsiteContent"
            )[0]
            const containerContentHeight = containerContent.offsetHeight
            console.log(containerContentHeight)
            containerContent.style.marginTop = containerContent.classList.contains(
              "inactive"
            )
              ? "-" + containerContentHeight + "px"
              : "0px"
          })
          .then(() => {
            container.getElementsByClassName("content")[0].style.transition =
              "all 0.5s ease-in-out"
          })
      })
    }

    const MoveContent = () => {
      const parallaxContents = document.getElementsByClassName(
        "vertical-parallax"
      )
      Array.from(parallaxContents).forEach(content => {
        var viewportOffset = content.getBoundingClientRect()
        const bottomOffset = 0
        const topOffset = 0
        let percentageHeightPositionOnScreen =
          (viewportOffset.top -
            window.innerHeight * topOffset +
            content.offsetHeight * 0.5) /
          (window.innerHeight * (1 - topOffset - bottomOffset))

        percentageHeightPositionOnScreen = Math.max(
          0,
          Math.min(1, percentageHeightPositionOnScreen)
        )
        let transform = `translateY(${percentageHeightPositionOnScreen * 100}%)`
        content.style.transform = transform
      })
    }

    document.addEventListener("scroll", () => {
      if (window.innerWidth >= 900) MoveContent()
    })

    if (window.innerWidth < 900) {
      HideAndShowContent()
    }
  })
  const [activeContent, setActiveContent] = useState(false)
  const contentToggle = () => {
    setActiveContent(x => !x)
  }

  return (
    <StyledContainer className="container">
      <TitleImageContainer className="imgTitleContainer">
        <StyledTitle className={className}>{title}</StyledTitle>
        <WebsiteImage
          className="WebsiteImage"
          alt={alt}
          src={src}
          onClick={contentToggle}
        />
      </TitleImageContainer>
      <Indicator
        active={activeContent ? "active" : ""}
        onClick={contentToggle}
      />
      <WebsiteContent
        className={activeContent ? "WebsiteContent" : "inactive WebsiteContent"}
        description={description}
        technologies={technologies}
        color={color}
        title={title}
        link={link}
      />
    </StyledContainer>
  )
}

export default WebsiteCard
