import React from "react"
import styled from "styled-components"
import { Menu } from "@styled-icons/feather/Menu"
import { MenuAlt3 } from "@styled-icons/heroicons-outline/MenuAlt3"
import { X } from "@styled-icons/heroicons-outline/X"

const StyledToggle = styled(MenuAlt3)`
  display: none;

  @media (max-width: 1000px) {
    display: block;
    position: relative;
    margin-right: 10px;
    width: 45px;
    height: 45px;
    cursor: pointer;
  }
  @media (max-width: 300px) {
    width: 30px;
    height: 30px;
  }
`
const StyledToggleX = styled(X)`
  display: none;
  position: relative;
  margin-right: 10px;
  width: 45px;
  height: 45px;
  cursor: pointer;

  @media (max-width: 300px) {
    width: 30px;
    height: 30px;
  }
`
