import React from "react"
import styled from "styled-components"
import SEO from "../components/seo"
import MainTemplate from "../templates/MainTemplate"
import Background from "../components/atoms/Background"
import TextBox from "../components/atoms/TextBox"
import WebsitesList from "../components/organisms/WebsitesList"
import SectionHeader from "../components/atoms/SectionHeader"
import AboutMeSection from "../components/organisms/AboutMeSection"
import MenuContainer from "../components/organisms/MenuContainer"
import MediaIcons from "../components/atoms/MediaIcons"
import StickyMediaIcons from "../components/atoms/StickyMediaIcons"

const HomeSection = styled.section`
  width: 100%;
  min-height: 100vh;
  overflow: auto;
  position: relative;
  :before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 150px;
    z-index: 1;
    background: linear-gradient(to top, #fff, transparent);
    @media (max-width: 800px) {
      height: 50px;
    }
  }
`

const StyledSection = styled.section`
  width: 100%;
  /* min-height: 100vh; */
  /* overflow: auto; */
  padding-top: 50px;
  position: relative;
`

const ContactSection = styled.section`
  width: 100%;
  height: 350px;
  text-align: center;
  margin-top: 150px;
  position: relative;
  background-color: #eee;
  :before {
    content: "";
    width: 50%;
    height: 100px;
    position: absolute;
    top: -48px;
    left: 0;
    background: #eee;
    transform: skewY(6deg);
    z-index: -10;
  }
  :after {
    content: "";
    width: 50%;
    height: 100px;
    position: absolute;
    top: -48px;
    right: 0;
    background: #eee;
    transform: skewY(-6deg);
    z-index: -10;
  }
`

const StyledContactName = styled.h2`
  position: relative;
  padding-top: 100px;
  display: flex;
  justify-content: center;
  color: #333;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 2.5vw;
  cursor: default;
  letter-spacing: 3px;
  @media (max-width: 500px) {
    flex-direction: column;
    font-size: 5vw;
  }
`

const Home = () => (
  <MainTemplate>
    <Background />
    <MenuContainer />
    <SEO title="Emilia Flisikowska" />
    <StickyMediaIcons />
    <HomeSection id="home">
      <TextBox />
    </HomeSection>
    <StyledSection id="aboutMe">
      <SectionHeader title="About me" />
      <AboutMeSection />
    </StyledSection>
    <StyledSection id="portfolio">
      <SectionHeader title="Portfolio" />
      <WebsitesList />
    </StyledSection>
    <ContactSection id="contact">
      {/* <TopContact /> */}
      <StyledContactName>
        <span>Front-end developer-</span>
        <span
          style={{
            color: "#777",
          }}
        >
          Emilia Flisikowska
        </span>
      </StyledContactName>
      <MediaIcons />
    </ContactSection>
  </MainTemplate>
)

export default Home
